import React from "react";
import { UserPick } from "./UserPick";
import { HousePick } from "./HousePick";
import { Result } from "./Result";
import items from "./items.json";

const Ring = (props) => {
  //define user move
  const [Upicked, setUpicked] = React.useState("");
  //define computer move
  const [Hpicked, setHpicked] = React.useState("");
  //you lose ||you win||draw
  const [Message, setMessage] = React.useState("");
  //Whose the winner
  const [isWinner, setIsWinner] = React.useState(true);
  //track game phase
  const [Status, setStatus] = React.useState(false);
  //onclick
  const pick = (item, i) => {
    console.log(i);
    //chose random number for computer
    let arr = [0, 1, 2].filter((a) => a !== i);
    const r = arr[Math.floor(Math.random() * 2)];
    setIsWinner(items.cases[i][r]);
    //user picked=> change phase
    setStatus(() => true);
    //return item picked
    setUpicked(() => item);
    //set items picked from random number after 2s
    setHpicked(items["hand" + r]);
    console.log(i, r, items.cases[i][r]);
    setMessage(items.cases[i][r]);
    props.onClick(items.cases[i][r]);
  };
  //reset game
  const reset = () => {
    //set initial states
    setUpicked("");
    setHpicked("");
    setMessage("");
    setStatus(false);
  };
  return (
    <>
      {!Status ? (
        <>
          <div id="phase_set">
            <div id="triangle-box">
              <img src={items.traingle} alt="" id="triangle" />
              <div
                className="btn-outline"
                id="paper"
                onClick={() => pick(items["hand0"], 0)}
              >
                <div className="btn-inner">
                  <img
                    src={items["hand0"].img}
                    width="51px"
                    height="58px"
                    alt={items["hand0"].name}
                  />
                </div>
              </div>
              <div
                className="btn-outline"
                id="scissors"
                onClick={() => pick(items["hand1"], 1)}
              >
                <div className="btn-inner">
                  <img
                    src={items["hand1"].img}
                    width="51px"
                    height="58px"
                    alt={items["hand1"].name}
                  />
                </div>
              </div>
              <div
                className="btn-outline"
                id="rock"
                onClick={() => pick(items["hand2"], 2)}
              >
                <div className="btn-inner">
                  <img
                    src={items["hand2"].img}
                    width="51px"
                    height="58px"
                    alt={items["hand2"].name}
                  />
                </div>
              </div>
            </div>
          </div>
        </>
      ) : (
        <>
          <div id="phase_get">
            <div id="userpick">
              <p>You picked</p>
              <UserPick p={Upicked} iswinner={isWinner} />
            </div>
            <div id="outcame">
              <Result onClick={() => reset()} m={Message} />
            </div>
            <div id="housepick">
              <p>The House picked</p>
              <HousePick p={Hpicked} iswinner={isWinner} />
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default Ring;
