import React from "react";
import Ring from "./Ring.js";
import Modal from "./modal.js";
import { useModalContext } from "./ModalContext.js";
const Game = () => {
  const { show } = useModalContext();
  let storedScore;
  storedScore =
    document.cookie &&
    document.cookie
      .split(";")
      .find((i) => i.startsWith("score="))
      .split("=")[1];
  const [score, setScore] = React.useState(storedScore ? storedScore : 0);
  const [showIndex, setShowIndex] = React.useState(false);
  const [index, setIndex] = React.useState("");
  const [indexColor, setIndexColor] = React.useState("");
  const getScore = (i) => {
    console.log("i", i);
    if (i) {
      setScore(Number(score) + 1);
      setIndexColor("text-success");
      setIndex("+1");
      setShowIndex(true);
    } else {
      setScore(Number(score) - 1);
      setIndexColor("text-danger");
      setIndex("-1");
      setShowIndex(true);
    }
  };
  React.useEffect(() => {
    const delta = setTimeout(() => setShowIndex(false), 1000);
    return () => {
      clearTimeout(delta);
    };
  }, [score]);
  return (
    <>
      <header id="header">
        <div>
          <img src="images/logo.svg" alt="logo" />
        </div>
        <div id="score-card">
          <p id="score-label">score</p>
          <p id="score">
            {score}
            <i className={indexColor} id="index">
              {showIndex && index}
            </i>
          </p>
        </div>
      </header>
      <main id="main">
        <Ring onClick={(scr) => getScore(scr)} />
      </main>
      <footer id="footer">
        <button id="rules" onClick={show}>
          Rules
        </button>
        <Modal />
        <div className="attribution">
          Challenge by{" "}
          <a
            href="https://www.frontendmentor.io?ref=challenge"
            target="_blank"
            rel="noreferrer"
          >
            Frontend Mentor
          </a>
          . Coded by <a href="houcine-code.github.io">Elhoucine</a>.
        </div>
      </footer>
    </>
  );
};
export default Game;
